<style scoped>
.layout {
  border: 1px solid #d7dde4;
  background: #f5f7f9;
  position: relative;

  height: 800px;
}
.layout-header-bar {
  background: #5b6270;
  color: #d7dde4;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
}
.layout-logo-left {
  width: 90%;
  height: 30px;
  background: #5b6270;
  border-radius: 3px;
  margin: 15px auto;
}
.menu-icon {
  transition: all 0.3s;
}
.rotate-icon {
  transform: rotate(-90deg);
}
.menu-item span {
  display: inline-block;
  overflow: hidden;
  width: 69px;
  text-overflow: ellipsis;
  white-space: nowrap;
  vertical-align: bottom;
  transition: width 0.2s ease 0.2s;
}
.menu-item i {
  transform: translateX(0px);
  transition: font-size 0.2s ease, transform 0.2s ease;
  vertical-align: middle;
  font-size: 16px;
}
.collapsed-menu span {
  width: 0px;
  transition: width 0.2s ease;
}
.collapsed-menu i {
  transform: translateX(5px);
  transition: font-size 0.2s ease 0.2s, transform 0.2s ease 0.2s;
  vertical-align: middle;
  font-size: 22px;
}
</style>
<template>
  <div class="layout">
    <Layout style="height:800px;">
      <Sider ref="side1" hide-trigger collapsible :collapsed-width="78" v-model="isCollapsed">
        <Menu active-name="1-1" theme="dark" width="auto" :class="menuitemClasses">
          <MenuItem name="1-1">
            <Icon type="md-barcode"/>
            <router-link style="color: #f5f7f9;" to="running_state">运行状态</router-link>
          </MenuItem>
          <MenuItem name="1-2">
            <Icon type="ios-at-outline"/>
            <router-link style="color: #f5f7f9;" to="online_channels">在线列表</router-link>
          </MenuItem>
          <MenuItem name="1-3">
            <Icon type="ios-apps-outline"/>

            <router-link style="color: #f5f7f9;" to="plugin_list">插件列表</router-link>
          </MenuItem>
                    <MenuItem name="1-4">
           <Icon type="ios-bug-outline" />
            <router-link style="color: #f5f7f9;" to="websocket">在线测试</router-link>
          </MenuItem>
        </Menu>
      </Sider>
      <Layout>
        <!-- <Header :style="{padding: 0}" class="layout-header-bar"></Header>-->
        <Content :style="{margin: '20px', background: '#fff', minHeight: '250px'}">
          <!--显示内容区域-->
          <router-view></router-view>
        </Content>
      </Layout>
    </Layout>
  </div>
</template>
<script>
let config = require("../config/config.json");

export default {
  data() {
    return {
      isCollapsed: false,
      localNodeInfo: {}
    };
  },
  created() {
    this.$router.push("running_state");
    this.loadData();
  },
  computed: {
    rotateIcon() {
      return ["menu-icon", this.isCollapsed ? "rotate-icon" : ""];
    },
    menuitemClasses() {
      return ["menu-item", this.isCollapsed ? "collapsed-menu" : ""];
    }
  },
  methods: {
    /**
      从服务器请求数据
      http://localhost:2580/dashboard/localNodeInfo


       */
    loadData() {
      let _this = this;
      console.log("加载数据:");
      this.axios
        .get(
          "http://" +
            config.host +
            ":" +
            config.port +
            config.context +
            "dashboard/localNodeInfo",
          {
            params: {},
            headers: {
              web_console_token: localStorage.getItem("web_console_token")
            }
          }
        )
        .then(response => {
          console.log("本地节点数据:", response.data.state == 1, response.data);
          if (response.data.state == 1) {
            _this.localNodeInfo = response.data.data;
          } else {
            this.$Message.error("数据请求失败");
          }
        })
        .catch(response => {
          console.log(response);
        });
    },
    collapsedSider() {
      this.$refs.side1.toggleCollapse();
    }
  }
};
</script>
